// banner
.index-banner{
  background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/banner.png');
  height: 840px;
  @include phone{
    height: 394px;
    
  }
}

.description{
  padding: 64px 0px;
}

.description li{
  width: 21%;
  
}
@include pad{
  .description-list{
    flex-wrap: wrap;
    @include phone{
      justify-content: center;
    }
  }
  .description-list li{
    width: 40%;
    margin-top: 0.8em;
    @include phone{
      width: 60%;
    }
  }
}

.classic{
  padding: 64px 0px;
  @include pad{
    font-size: 11px;
  }
}
.classicImg{
  background: url(../images/product-1.png) no-repeat center;
  background-size: cover;
  width: 635px;
  height: 320px;
  @include pad{
    width: 390px;
    height: 168px;
  }
  @include phone{
    width: 100%;
    height: 134px;
  }
}
.classicImg2{
  background-image: url(../images/product-2.png);
}
.classicImg3{
  background-image: url(../images/product-3.png);
}
.classic-li {
  @include pad{
    flex-direction: row;
    justify-content: start;
  }
}

.jointname{
  background: url(../images/jointname-bg.png) no-repeat center;
  background-size: cover;
  @include pad{
    font-size: 12px;
  }
}

.jointnameImg{
  max-width: 540px;

  @include pad{
    width: 48%;
  }
  @include phone{
    width: 100%;
    max-width: none;
  }
  img{
    @include phone{
      width: 100%;
    }
  }
}

.img-bg{}
.img-bg img{width:100%;}

.textarea{height: 136px;}
